@font-face {
    font-family: 'icomoon';
    src:    url('fonts/icomoon.eot?q0uxrd');
    src:    url('fonts/icomoon.eot?q0uxrd#iefix') format('embedded-opentype'),
        url('fonts/icomoon.ttf?q0uxrd') format('truetype'),
        url('fonts/icomoon.woff?q0uxrd') format('woff'),
        url('fonts/icomoon.svg?q0uxrd#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
